<template>
  <div class="lm-project">
    <!--  -->
    <div v-for="item in projectArr" :key="item.id" class="lm-img-item">
      <el-image :src="item.img" :alt="item.title" style="width: 500px; height: 250px" />
      <div class="imgText">
        <h3>{{ item.title }}</h3>
        <el-button v-show="item.herf" type="text"><a :href="item.herf" target="_blank">去看看</a></el-button>
      </div>
    </div>
  </div>
</template>

<script>
import src from '@/assets/nsct.png'
import zhbjSrc from '@/assets/zhbj.png'
import qdySrc from '@/assets/qdy.png'
import zcSrc from '@/assets/zc.png'
import qmy from '@/assets/qmy.png'
export default {
  data() {
    return {
      projectArr: [
        {
          id: 1,
          title: '广州南沙企业集群地址管理服务系统',
          img: src,
          herf: 'http://www.nsjqzc.com/#/'
        },
        {
          id: 2,
          title: '综合办件平台服务商管理系统',
          img: zhbjSrc,
          herf: ''
        },
        {
          id: 3,
          title: '增城区企业开办一站式服务平台管理系统',
          img: zcSrc,
          herf: ''
        },
        {
          id: 4,
          title: '全电易服务商管理系统',
          img: qdySrc,
          herf: ''
        },
        {
          id: 5,
          title: '企名易',
          img: qmy,
          herf: 'https://q.g4b.cn/nameapply/#/index'
        }
      ]
    }
  }
}
</script>

<style lang="scss">
  @keyframes mymove
  {
    0% {
      bottom: 0;
    }
    50% {
      bottom: 50;
    }
    100% {
      bottom: 100;
    }
  }
  .lm-project {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    .lm-img-item {
      margin-bottom: 10px;
      position: relative;
      overflow: hidden;
      .imgText {
        position: absolute;
        bottom: -100px;
        box-sizing: border-box;
        z-index: 999;
        width: 100%;
        height: 50px;
        padding: 10px;
        display: flex;
        justify-content: space-between;
        transition: all .5s;
        align-items: center;
        background-color: rgba($color: #000000, $alpha: .7);
        h3 {
          font-size: 18px;
          color: #ddd;
        }
        a {
          color: #409eff;
          text-decoration: none;
        }
      }
    }
    .lm-img-item:hover {
      .imgText {
        bottom: 0;
      }
    }
  }
  .el-carousel__item {
    img {
      display: block;
      width: 100%;
    }
  }
  .el-carousel__item h3 {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    line-height: 200px;
    margin: 0;
  }

  .el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
  }

  .el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
  }
</style>



