<template>
  <div class="lm-avatar">
    <!--  -->
    <div class="header">
      <div class="header-o">
        <!--  -->
        <img :src="imgSrc" alt="">
      </div>
    </div>
    <div class="content" @click="toNext">
      <h1>Hello,I'm LinMing</h1>
      <p>我叫林鸣</p>
      <p>一名前端开发工程师</p>
      <p>linmingku@163.com</p>
    </div>
  </div>
</template>
<script>
import imgSrc from '@/assets/11.jpg'
export default {
  data() {
    return {
      imgSrc,
      size: 'small',
      circleUrl: 'https://cube.elemecdn.com/3/7c/3ea6beec64369c2642b92c6726f1epng.png'
    }
  },
  methods: {
    toNext() {
      this.$emit('tonext')
    }
  }
}
</script>

<style lang="scss" scoped>
  .lm-avatar {
    width: 500px;
    // border: 1px solid #ddd;
    border-radius: 10px;
    z-index: 999;
    cursor: pointer;
    position: relative;
    .content {
      text-align: center;
      padding: 10px 0;
      border-radius: 10px;
      background-color: rgba(255, 255, 255, .3);
      // background-color: #fff;
      h1 {
        font-size: 40px;
        font-weight: 800;
      }
      p {
        margin: 10px 0;
        font-size: 18px;
      }
    }
    .header-o {
      position: absolute;
      left: 50%;
      margin-left: -50px;
      top: -105px;
      width: 100px;
      height: 100px;
      overflow: hidden;
      border-radius: 50%;
      border: 1px solid #ccc;
      img {
        display: block;
        width: 100%;
      }
    }
  }
  .lm-avatar:hover {
    animation:mymove 3s infinite;
  }
  @keyframes mymove
  {
    from {
      box-shadow: 0px 0px 15px 7px #bbb;
    }
    to {
      // left:520px;
      // padding: 20px 0;
      box-shadow: 0px 0px 20px 14px #bbb;
    }
  }
</style>



