<template>
  <div>
    <full-page ref="page" :options="options">
      <div class="section">
        <div class="box1">
          <!-- <video :src="videoSrc" autoplay="autoplay" muted="muted" loop="loop" class="mit-video"/> -->
          <!-- <video controls autoplay="autoplay" muted="muted" loop="loop" class="mit-video" width="400" height="300">
            <source :src="videoSrc" type="video/mp4">
          </video> -->
          <!-- <video :src="videoSrc" controls class="video_back" type="video/mp4" muted autoplay="autoplay" loop="loop">
            您的浏览器不支持
          </video> -->
          <lmAvatar @tonext="tonext"/>
        </div>
      </div>
      <div class="section">
        <!--  -->
        <div class="lm-job sectionJob">
          <el-card class="box-card" style="width: 500px">
            <div slot="header" class="clearfix">
              <span>广州网融信息技术有限公司</span>
            </div>
            <div>
              <h3>工作职责：</h3>
              <ol>
                <li>
                  1、负责公司各网站项目制作和上线、小程序等需求的开发
                </li>
                <li>
                  2、需求，配合后台开发人员实现页面和相关交互功能，维护及优化前端页面性能
                </li>
              </ol>
            </div>
            <!-- <div v-for="o in 4" :key="o" class="text item">
              {{ '列表内容 ' + o }}
            </div> -->
          </el-card>
        </div>
      </div>
      <div class="section">
        <div class="box2">
          <div class="lm-skills">
            <h3>My Skills</h3>
            <ul>
              <li @mousemove="change(80)" @mouseleave="change(0)">HTML</li>
              <li @mousemove="change(70)" @mouseleave="change(0)">CSS</li>
              <li @mousemove="change(60)" @mouseleave="change(0)">JavaScript</li>
              <li @mousemove="change(75)" @mouseleave="change(0)">Vue</li>
              <li @mousemove="change(40)" @mouseleave="change(0)">React</li>
            </ul>
            <div id="main" style="height: 500px;" />
          </div>
        </div>
      </div>
      <div class="section">
        <div class="box3">
          <lmProject/>
        </div>
      </div>
      <!-- <div class="section"> -->
      <!--  -->
      <!-- </div> -->
    </full-page>
  </div>
</template>

<script>
import lmAvatar from '@/components/linming/lmAvatar'
import lmProject from '@/components/linming/lmProject'
export default {
  name: '',
  components: {
    lmAvatar,
    lmProject
  },
  data() {
    return {
      options: {
        menu: '#menu',
        licenseKey: 'OPEN-SOURCE-GPLV3-LICENSE',
        afterLoad: this.afterLoad,
        showActiveTooltip: true,
        slidesNavigation: true,
        verticalCentered: false,
        scrollOverflow: true,
        navigation: true, // 是否显示导航，默认为false
        navigationPosition: 'right', // 导航小圆点的位置
        scrollBar: false,
        animateAnchor: false,
        controlArrows: true,
        css3: true,
        keyboardScrolling: false, // 是否可以使用键盘方向键导航，默认为true
        continuousVertical: false, // / 是否循环滚动，默认为false。如果设置为true，则页面会循环滚动，而不像loopTop或loopBottom那样出现跳动，注意这个属性和loopTop、loopBottom不兼容和，不要同时设置
        // navigation: true,
        anchors: ['首页', '工作', '技能', '作品'],
        sectionsColor: ['#fff', '#fff', '#fff', '#fff', '#1bcee6', '#ee1a59', '#2c3e4f', '#ba5be9', '#b4b8ab']
        //           //为每个section设置背景色
        //           sectionsColor: ["#f00","#0f0","#00f"],
        // //用来控制slide幻灯片的箭头，设置为false，两侧的箭头会消失
        //           controlArrows: false,
        // //每一页幻灯片的内容是否垂直居中
        //           verticalCentered: false,
        // //字体是否随着窗口缩放而缩放
        //           resize: true,
        // //页面滚动速度
        //           scrollingSpeed: 700,
        // //定义锚链接，用户可以快速打开定位到某一页面；不需要加"#"，不要和页面中任意的id和name相同
        //           anchors: ["page1","page2","page3"],
        // //是否锁定锚链接
        //           lockAnchors: true,
        // //定义section页面的滚动方式，需要引入jquery.easings插件
        //           easing:,
        // //是否使用css3 transform来实现滚动效果
        //           css3: false,
        // //滚动到最顶部后是否连续滚动到底部
        //           loopTop: true,
        // //滚动到最底部后是否连续滚动到顶部
        //           loopBottom: true,
        // //横向slide幻灯片是否循环滚动
        //           loopHorizontal: false,
        // //是否循环滚动，不会出现跳动，效果很平滑
        //           continuousVertical: true,
        // //是否使用插件滚动方式，设为false后，会出现浏览器自带的滚动条，将不会按页滚动
        //           autoScrolling: false,
        // //是否包含滚动条，设为true，则浏览器自带的滚动条会出现，页面还是按页滚动，但是浏览器滚动条默认行为也有效
        //           scrollBar: true,
        // //设置每个section顶部的padding，当我们要设置一个固定在顶部的菜单、导航、元素等时使用
        //           paddingTop: "100px",
        // //设置每个section底部的padding，当我们要设置一个固定在底部的菜单、导航、元素等时使用
        //           paddingBottom: "100px",
        // //固定的元素，为jquery选择器；可用于顶部导航等
        //           fixedElements: ".nav",
        // //是否可以使用键盘方向键导航
        //           keyboardScrolling: false,
        // //在移动设置中页面敏感性，最大为100，越大越难滑动
        //           touchSensitivity: 5,
        // //设为false，则通过锚链接定位到某个页面不再有动画效果
        //           animateAnchor: false,
        // //是否记录历史，可以通过浏览器的前进后退来导航
        //           recordHistory: true,
        // //绑定菜单，设定相关属性和anchors的值对应后，菜单可以控制幻灯片滚动
        //           menu: '.nav',
        // //是否显示导航，设为true会显示小圆点作为导航
        //           navigation: true,
        // //导航小圆点的位置，可以设置为left或者right
        //           navigationPosition: right,
        // //鼠标移动到小圆点上时显示出的提示信息
        //           navigationTooltips: ["第一页","第二页","第三页"],
        // //是否显示当前页面小圆点导航的提示信息，不需要鼠标移上
        //           showActiveTooltip: true,
        // //是否显示横向幻灯片的导航
        //           slidesNavigation: true,
        // //横向幻灯片导航的位置，可以为top或者bottom
        //           slidesNavPosition: bottom,
        // //内容超过满屏时是否显示滚动条，需要jquery.slimscroll插件
        //           scrollOverflow: true,
        // //section选择器
        //           sectionSelector: ".section",
        // //slide选择器
        //           slideSelector: ".slide"
      },
      chartsVal: 0
    }
  },
  mounted() {
    this.drawLine(0)
  },
  methods: {
    tonext() {
      this.$refs.page.api.moveSectionUp()
    },
    change(val) {
      this.drawLine(val)
    },
    drawLine(chartsVal) {
      // var i=0;
      // 基于准备好的dom，初始化echarts实例
      const myChart = this.$echarts.init(document.getElementById('main'))
      const option = {
        tooltip: {
          formatter: '{a} <br/>{b} : {c}%'
        },
        toolbox: {
          feature: {
            restore: {},
            saveAsImage: {}
          }
        },
        series: [
          {
            name: 'Skills',
            type: 'gauge',
            detail: { formatter: '{value}%' },
            data: [{ value: chartsVal, name: '掌握程度' }]
          }
        ]
      }
      // const color = ['#19D672', '#FD517D']
      // 绘制图表
      myChart.setOption(option, true)
    },
    click() {
      // vue调用fullpapge的方法
      this.$refs.page.moveSectionUp()
      // moveSectionDown();
      // /向上滚动一页
      // moveSectionUp();
      // //向下滚动一页
      // moveSectionDown();
      // //滚动到第几页，第几个幻灯片；页面从1计算，幻灯片从0计算
      // moveTo(wection,slide);
      // //和moveTo一样，但是没有动画效果
      // silentMoveTo(section,slide);
      // //幻灯片向右滚动
      // moveSlideRight();
      // //幻灯片向左滚动
      // moveSlideLeft();
      // //动态设置autoScrolling配置项
      // setAutoScrolling(boolean);
      // //动态设置lockAnchors配置项
      // setLockAnchors(boolean);
      // //动态设置recordHistory配置项
      // setRecordHistory(boolean);
      // //动态设置scrollingSpeed配置项
      // setScrollingSpeed(milliseconds);
      // //添加或删除鼠标/滑动控制，第一个参数为启用、禁用；第二个参数为方向，取值包含all、up、dowm、left、right，可以使用多个，逗号分隔
      // setAllowScrolling(boolean,[directions]);
      // //销毁fullpage特效，不写type，fullpage给页面添加的样式和html元素还在；如果使用all，则样式和html等全部被销毁
      // destroy(type);
      // //重新更新页面和尺寸，用于通过ajax请求后改变了页面结构之后，重建效果
      // reBuild();
    }
  }
}
</script>

<style scoped lang="scss">
  div {
    margin: 0;
    padding: 0;
  }
  .page2 {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .section {
    .box1 {
      display: flex;
      height: 100%;
      justify-content: center;
      align-items: center;
      background-image: url('../../assets/bg.gif');
      .video_back {
        pointer-events: none;
        /*设置视频最小宽度和高度*/
        // min-width: 100%;
        // min-height: 100%;
        /**/
        width:auto;
        height:auto;
        /*生成绝对定位的元素，相对于浏览器窗口进行定位。*/
        position: fixed;
        /*这里我试了一下好像只要是矩形的四个角的的点都行,也就是说right:0;bottom:0;也可以*/
        left: 0;
        top: 0;
        /*将视频放在元素的堆叠顺序最底层，以防覆盖其他东西*/
      }
    }
    .box2 {
      // width: 1000px;
      display: flex;
      // background-color: #ccc;
      width: 100%;
      height: 100%;
      // background-image: url('../../assets/2.jpg');
      background-repeat:no-repeat;
      background-size: 100%;
      // filter: blur(10px);
      justify-content: space-between;
      align-items: center;
      margin: 0 auto;
      .lm-skills {
        width: 1300px;
        margin: 0 auto;
        display: flex;
        flex-direction: column;
        // justify-content: space-around;
        align-items: center;
        ul{
          display: flex;
        }
        h3 {
          font-weight: 800;
          font-size: 40px;
          margin-bottom: 20px;
          font-style: italic;
          color: #333;
        }
        li {
          width: 100px;
          cursor: pointer;
          border: 1px solid #ddd;
          border-radius: 10px;
          padding: 10px 20px;
          text-align: center;
          margin: 0 10px;
        }
        li:hover {
          background-color: #eee;
          color: #333;
        }
      }
      #main {
        width: 500px;
      }
    }
    .box3 {
      width: 1200px;
      height: 100%;
      margin: 0 auto;
      display: flex;
      align-items: center;
    }
  }
  .lm-job {
      h3 {
        font-size: 15px;
        font-weight: 600;
        padding: 0 0 10px 0;
      }
    }
  .sectionJob {
    width: 1300px;
    height: 100%;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    align-items: center;
  }
</style>



